@import url(https://fonts.googleapis.com/css?family=Oswald);
@import url(https://fonts.googleapis.com/css?family=Open+Sans+Condensed:300);

.heading {
    background-color: rgba(0, 0, 0, 0.904);
    width: 30vw;
    text-align: center;
    margin-top: 4px;
    padding: 12px;
    color: white;
    border-radius: 12px;
    font-family: "Oswald", sans-serif;
}

h1 {
    font-size: 3em;
    padding-bottom: 0;
    margin-bottom: 0;
}

.heading h3 {
    font-family: "Open Sans Condensed", sans-serif;
}
  
hr {
    width: 60%;
}
.ingredients {
    background-color: rgba(0, 0, 0, 0.904);
    width: 30vw;
    text-align: center;
    margin-top: 10px;
    padding: 12px;
    color: white;
    border-radius: 12px;
    font-family: "Oswald", sans-serif;
}

.ingredientsList {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    text-align: justify;
    margin: auto;
}

ul {
    list-style-type:disc;
   
}
.steps {
    background-color: rgba(0, 0, 0, 0.904);;
    width: 30vw;
    text-align: center;
    margin-top: 10px;
    padding: 12px;
    color: white;
    border-radius: 12px;
    font-family: "Oswald", sans-serif;
}
.stepsList li {
    padding: 2px;
    text-align: justify;
    list-style-type: decimal;
}

span {
    padding-left: 10px;
}
.footer{
    background-color: rgba(0, 0, 0, 0.904);
    width: 30vw;
    text-align: center;
    margin-top: 10px;
    padding: 12px;
    padding-top: 5px;
    padding-bottom: 5px;
    color: white;
    border-radius: 12px;
    font-family: "Oswald", sans-serif;
}

h4 {
    border-bottom: 1px dotted white;
    padding-bottom: 5px;
    border-bottom: 20%;
}

.footerRecipe ul {
    padding: 0;
}
.footerRecipe li {
    text-align: justify;
    list-style-type: none;

}
.footerRecipe {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    text-align: justify;
    margin: auto;
}

span {
    padding-left: 10px;
}
body {
    background-image: url(/static/media/kim-daniels-p6XuDW03YjE-unsplash.9b03b01b.jpg);
    background-size: cover;
    display: flex;
    flex-direction: column;
    align-items: center;
}
